import { DateCalendar, LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import React, { useState, useEffect } from 'react';
import dayjs from 'dayjs';
import { Button, Typography } from '@mui/material';
import BackButton from '../Components/BackButton';
import { IoMdPlay } from 'react-icons/io';

export default function Simulation() {
  const [currentDate, setCurrentDate] = useState(dayjs('2023-01-01'));
  const [isRunning, setIsRunning] = useState(false);

  useEffect(() => {
    let timer;
    if (isRunning) {
      timer = setInterval(() => {
        setCurrentDate((prevDate) => {
          const nextDate = prevDate.add(1, 'day');
          if (nextDate.year() > 2023) {
            clearInterval(timer);
            setIsRunning(false);
            return prevDate;
          }
          return nextDate;
        });
      }, 500);
    }
    return () => clearInterval(timer);
  }, [isRunning]);

  const startSimulation = () => {
    setCurrentDate(dayjs('2023-01-01'));
    setIsRunning(true);
  };

  return (
    <div className="App">
      <header className="App-header">
        <BackButton />
        <Typography variant="h3" style={{ fontWeight: 'bold' }}>
          ELPRIS VS. FJERNVARMEPRIS
        </Typography>
        <LocalizationProvider dateAdapter={AdapterDayjs}>
          <DateCalendar value={currentDate} readOnly views={['day']} />
        </LocalizationProvider>

        <Button
          variant="contained"
          onClick={startSimulation}
          disabled={isRunning}
          startIcon={<IoMdPlay />}
        >
          Start Simulation
        </Button>
      </header>
    </div>
  );
}
